        },
        {
          id: 4,
          name: "Guindaste",
          subtypes: ['Até 48 toneladas', 'Acima de 48 toneladas']
        },
      ],
    }),

    computed: {
      CNPJMask: { //parei aqui
        get () {
          return this.company.edited.CNPJ.length <= 14
            ? '###.###.###-##' 
            : '##.###.###/####-##'
        },
        set () {
          return this.company.edited.CNPJ.length <= 14
            ? '###.###.###-##' 
            : '##.###.###/####-##'
        }
      },
      contactMask1 () {
        return this.contactType1 === 'Celular' ? '(##) # ####-####' : ''
      },
      contactMask2 () {
        return this.contactType2 === 'Celular' ? '(##) # ####-####' : ''
      },
      contactMask3 () {
        return this.contactType3 === 'Celular' ? '(##) # ####-####' : ''
      },
      rulesCEP () {
        const rules = []
        const rule = ( !this.items.cep || this.erroCep === false && this.items.cep.length === 8) || 'CEP inválido'
        rules.push(rule)

        return rules
      },

      companyFormTitle () {
        return this.editedLIndex === -1
          ? 'Nova Empresa'
          : `${this.companies.RAZAOSOCIAL}`
      },
      vehicleFormTitle () {
        return this.editedVIndex === -1
          ? 'Novo Veículo'
          : `Editar Veículo - ID ${this.vehicle.edited.ID}`
      },
      licenceFormTitle () {
        return this.editedLIndex === -1
          ? 'Nova Licença'
          : `Licença - ${this.licence.edited.AET} - ${this.licence.edited.CONJUNTO}`
      },

      rulesANOFAB () {
        const rules = []

        const rule = (this.vehicle.edited.ANOFAB >= 2000 || 'Veículo muito antigo para rodar')
        rules.push(rule)

        return rules
      },
    },

    watch: {
      // 'company.edited.CNPJ': {
      //   handler () {
      //     this.company.edited.CNPJ.length <= 14 ? this.CNPJMask = '###.###.###-##' : this.CNPJMask = '##.###.###/####-##'
      //   },
      //   deep: true
      // },

      companyDialog (val) {
        val || this.companyClose()
      },
      licenceDialog (val) {
        val || this.licenceClose()
      },

      companyDialogSave (val) {
        val || this.closecompanyDialogSave()
      },
      licenceDialogSave (val) {
        val || this.closeLicenceDialogSave()
      },

      companyDialogDelete (val) {
        val || this.vehicleCloseDelete()
      },
      licenceDialogDelete (val) {
        val || this.licenceCloseDelete()
      },

      err () {
        console.log(this.err)
        alert('Um erro foi encontrado. Por favor, entre em contato com o suporte.')
      },
      expanded () {
        this.fetchLicences()
      },
      licences () {
        this.licencesLoader = false
      }
    },

    created () {
      this.fetchCompanies()
      this.fetchFleet()
      this.maskCheckbox()
      Vue.config.warnHandler = (err => this.err = err)
    },

    beforeMount () {
      this.preencherCep()
    },

    methods: {
      getCep (cep) {
        return axios.get(`https://viacep.com.br/ws/${cep}/json/`)
      },
      preencherCep () {
        const cep = this.company.edited.CEP.replace(/\D/g, '')
        if (cep.length === 8) {
          this.getCep(cep)
            .then(r => {
              this.company.edited.ST = r.data.logradouro
              this.company.edited.UF = r.data.uf
              this.company.edited.CITY = r.data.localidade
              'erro' in r.data ? this.erroCep = true : this.erroCep = false
            })
        }
      },
      validate () {
        this.$refs.observer.validate()
      },
      maskCheckbox () {
        if (this.checkboxNF === true) {
          this.plateMask = 'NF-##############'
        } else {
          this.plateMask = 'AAA-#X##'
        }
      },
      activeVehicle () {
        if (this.checkboxAtivo === true) {
          this.vehicle.edited.ATIVO = 'S'
        } else {
          this.vehicle.edited.ATIVO = 'N'
        }
      },
      disabledField () {
        if (this.editedVIndex > -1) {
          this.isDisabled = true
        } else if (this.editedVIndex === -1) {
          this.isDisabled = false
        }
      },

      setTimeNow () {
        var date = new Date()
        var day = date.getDate()
        var month = date.getMonth() + 1
        var year = date.getFullYear()
        if (month < 10) {
          month = '0' + month
        }
        if (day < 10) {
          day = '0' + day
        }
        this.now = day + '/' + month + '/' + year

        if (this.editedVIndex === -1) {
          this.vehicle.edited.CADASTRO = this.now
        }
      },
      setEditTime () {
        const timeElapsed = Date.now()
        const rightNow = new Date(timeElapsed)
        this.timeNow = rightNow.toLocaleString('pt-BR')
      },

      setUser () {
        if (this.editedVIndex === -1) {
          this.vehicle.edited.USUARIO = this.user
        }
      },

      formatDate (tempDate) {
        if (!tempDate) {
          return null
        }

        const [year, month, day] = tempDate.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (tempDate) {
        if (!tempDate) {
          return null
        }

        const [day, month, year] = tempDate.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },

      // getColor (STATUS) {
      //   if (STATUS === 'PAGO') {
      //     return 'blue lighten-4'
      //   } else if (STATUS === 'A VENCER') {
      //     return 'green lighten-4'
      //   } else if (STATUS === 'TAXA PAGA') {
      //     return 'purple lighten-4'
      //   } else if (STATUS === 'A COBRAR') {
      //     return 'red lighten-4'
      //   } else if (STATUS === 'CONFERIR') {
      //     return 'orange lighten-4'
      //   } else if (STATUS === 'VENCIDO') {
      //     return 'red lighten-4'
      //   } else if (STATUS === 'NÃO COBRAR' || STATUS === 'ACERTO') {
      //     return 'red lighten-4'
      //   } else if (STATUS === '') {
      //     return ''
      //   } else { return 'grey lighten-2' }
      // },

      // getIcon (STATUS) {
      //   if (STATUS === 'PAGO') {
      //     return 'mdi-check-all blue--text'
      //   } else if (STATUS === 'A VENCER' || STATUS === 'A DEPOSITAR') {
      //     return 'mdi-cash-clock'
      //   } else if (STATUS === 'TAXA PAGA') {
      //     return 'mdi-check'
      //   } else if (STATUS === 'A COBRAR') {
      //     return 'mdi-clock-alert-outline'
      //   } else if (STATUS === 'CONFERIR') {
      //     return 'mdi-email-alert-outline'
      //   } else if (STATUS === 'VENCIDO') {
      //     return 'mdi-alert red--text'
      //   } else if (STATUS === 'NÃO COBRAR' || STATUS === 'ACERTO') {
      //     return 'mdi-cash-remove red--text'
      //   } else { return 'mdi-list-status' }
      // },

      fetchCompanies () {
        return fetch('https://sheetdb.io/api/v1/8nmjdq4kt9z0x')
          .then((r) => r.json())
          .then((r) => {
            this.companies = r
          })
      },
      fetchFleet () {
        return fetch('https://sheetdb.io/api/v1/gy59g6tp4jo4p')
          .then((r) => r.json())
          .then((r) => {
            this.fleet = r
          })
      },
      fetchLicences () {
        return fetch('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
          .then((res) => res.json())
          .then((res) => {
            this.licences = res
          })
      },

      greaterVehicleId () {
        return axios.get('https://sheetdb.io/api/v1/gy59g6tp4jo4p').then((r) => {
          this.vId = r.data.length + 1
          for (let i = 0; i < r.data.length; i++) {
            Number(this.vId) === Number(r.data[i].ID) ? this.repeatedV = true : this.repeatedV = false
          }
          if (this.editedVIndex === -1) {
            this.repeatedV === true ? alert('ID duplicado. Favor contatar suporte e suspender novos lançamentos até que a correção seja confirmada.') : this.vehicle.edited.ID = this.vId
          }
        })
      },
      greaterLicenceId () {
        return axios.get('https://sheetdb.io/api/v1/1q4dfgpu4na0b').then((res) => {
          this.lId = res.data.length + 148000
          for (let j = 0; j < res.data.length; j++) {
            Number(this.id) === Number(res.data[j].ID) ? this.repeatedL = true : this.repeatedL = false
          }
          if (this.editedLIndex === -1) {
            this.repeatedL === true ? alert('ID duplicado. Favor contatar suporte e suspender novos lançamentos até que a correção seja confirmada.') : this.licence.edited.ID = this.lId
          }
        })
      },
      getVehicleID (item) {
        this.licence.edited.IDVEICULO = item.ID
      },
      addLicence (item) {
        this.editedLIndex = this.licences.indexOf(item)
        this.licence.edited = Object.assign({}, item)
        this.licenceDialog = true
      },
      watchLicence (val) {
        this.editedLIndex = this.licences.indexOf(val)
        this.licence.edited = Object.assign({}, val)
        this.licenceWatchDialog = true
        console.log(val)
      },
      editCompany (item) {
        this.err.includes('Duplicado')
        ? (alert('Não atualizar lançamentos e contatar suporte.'), this.companyClose())
        : (this.editedCIndex = this.companies.indexOf(item), this.company.edited = Object.assign({}, item), this.companyDialog = true)
      }, // parei aqui
      editVehicle (item) {
        this.err.includes('Duplicado')
        ? (alert('Não atualizar lançamentos e contatar suporte.'), this.vehicleClose())
        : (this.editedVIndex = this.fleet.indexOf(item), this.vehicle.edited = Object.assign({}, item), this.companyDialog = true)
      },
      deleteItem (item) {
        this.editedVIndex = this.fleet.indexOf(item)
        this.vehicle.edited = Object.assign({}, item)
        this.companyDialogDelete = true
      },

      editVehicleConfirm () {
        // salva edição
        if (this.editedVIndex > -1) {
          axios.put(`https://sheetdb.io/api/v1/gy59g6tp4jo4p/ID/${this.vehicle.edited.ID}`, {
            data: {
              USUARIO: `${this.vehicle.edited.USUARIO}`,
              TARA: `${this.vehicle.edited.TARA}`,
              PLACA: `${this.vehicle.edited.PLACA}`,
              ATIVO: `${this.vehicle.edited.ATIVO}`,
              MARCA: `${this.vehicle.edited.MARCA}`,
              MODELO: `${this.vehicle.edited.MODELO}`,
              CONJUNTOPRINCIPAL: `${this.vehicle.edited.CONJUNTOPRINCIPAL}`,
              ANOFAB: `${this.vehicle.edited.ANOFAB}`,
              CHASSI: `${this.vehicle.edited.CHASSI}`,
              CARROCERIA: `${this.vehicle.edited.CARROCERIA}`,
              POTENCIA: `${this.vehicle.edited.POTENCIA}`,
              CMT: `${this.vehicle.edited.CMT}`,
              RENAVAM: `${this.vehicle.edited.RENAVAM}`,
              RNTRC: `${this.vehicle.edited.RNTRC}`,
              EDICAO: `${this.vehicle.edited.EDICAO}` + ` ${this.user} ${this.timeNow}.`,
            },
          })
            .then((response) => {
              if (response.status === 200 || response.status === 201) {
                this.showUpdate = !this.showUpdate
                this.fetchFleet()
                setTimeout(() => {
                  this.showUpdate = !this.showUpdate
                }, 1500)
              } else {
                this.showError = !this.showError
                setTimeout(() => {
                  this.showError = !this.showError
                  window.location.reload()
                }, 1500)
              }
            })
          this.companyDialogSave = false
          setTimeout(() => {
            this.vehicleClose()
          }, 200)
        } else {
          this.vehicleSave()
        }
      },
      deleteItemConfirm () {
        this.fleet.splice(this.editedVIndex, 1)
        this.vehicleCloseDelete()
      },

      companyClose () {
        this.companyDialog = false
        this.CNPJMask = '##.###.###/####-##'
        this.$nextTick(() => {
          this.company.edited = Object.assign({}, this.company.default)
          this.editedLIndex = -1
        })
      },
      licenceClose () {
        this.licenceDialog = false
        this.licenceWatchDialog = false
        this.$nextTick(() => {
          this.licence.edited = Object.assign({}, this.licence.default)
          this.editedLIndex = -1
        })
      },
      vehicleClose () {
        this.checkboxNF = false
        this.vehicle.edited.ATIVO = 'N'
        this.companyDialog = false
        this.isDisabled = false
        this.$nextTick(() => {
          this.vehicle.edited = Object.assign({}, this.vehicle.default)
          this.editedVIndex = -1
        })
      },

      closecompanyDialogSave () {
        this.companyDialogSave = false
      },
      vehicleCloseDelete () {
        this.companyDialogDelete = false
        this.$nextTick(() => {
          this.vehicle.edited = Object.assign({}, this.vehicle.default)
          this.editedVIndex = -1
        })
      },

      validateField () {
        this.$refs.form.validate()
      },

      vehicleClone () {
        if (this.editedVIndex > -1) {
          this.tempUSUARIO = this.vehicle.edited.USUARIO,
          this.tempTARA = this.vehicle.edited.TARA,
          this.tempPLACA = this.vehicle.edited.PLACA,
          this.tempATIVO = this.vehicle.edited.ATIVO,
          this.tempMARCA = this.vehicle.edited.MARCA,
          this.tempMODELO = this.vehicle.edited.MODELO,
          this.tempCONJUNTOPRINCIPAL = this.vehicle.edited.CONJUNTOPRINCIPAL,
          this.tempANOFAB = this.vehicle.edited.ANOFAB,
          this.tempCHASSI = this.vehicle.edited.CHASSI,
          this.tempPOTENCIA = this.vehicle.edited.POTENCIA,
          this.tempCARROCERIA = this.vehicle.edited.CARROCERIA,
          // 3.fechar o popup OK
          setTimeout(() => {
            this.vehicleClose()
          }, 300)
          // 4.abrir popup de novo pedido
          setTimeout(() => {
            document.getElementById('newVehicleBtn').click()
          }, 300)
          // 5.popular os campos com as informações salvas anteriormente
          setTimeout(() => {
            this.vehicle.edited.USUARIO = this.tempUSUARIO,
            this.vehicle.edited.TARA = this.tempTARA,
            this.vehicle.edited.PLACA = this.tempPLACA,
            this.vehicle.edited.ATIVO = this.tempATIVO,
            this.vehicle.edited.MARCA = this.tempMARCA,
            this.vehicle.edited.CONJUNTOPRINCIPAL = this.tempCONJUNTOPRINCIPAL,
            this.vehicle.edited.ANOFAB = this.tempANOFAB,
            this.vehicle.edited.CHASSI = this.tempCHASSI,
            this.vehicle.edited.POTENCIA = this.tempPOTENCIA,
            this.vehicle.edited.CARROCERIA = this.tempCARROCERIA
            this.vehicle.edited.MODELO = this.tempMODELO
          }, 300)
        } else {
          this.showVCloneBtn = false
        }
      },
      licenceClone () {
        if (this.editedLIndex > -1) {
          // this.tempCOMPRIMENTO = this.vehicle.edited.COMPRIMENTO,
          setTimeout(() => {
            this.licenceClose()
          }, 300)
          setTimeout(() => {
            document.getElementById('newLicenceBtn').click()
          }, 300)
          setTimeout(() => {
            // this.vehicle.edited.COMPRIMENTO = this.tempCOMPRIMENTO
          }, 300)
        } else {
          this.showLCloneBtn = false
        }
      },

      vehicleSave () {
        var form = document.getElementById('sheetdb-form-v')
        if (this.editedVIndex > -1) {
          this.companyDialogSave = true
        } else {
          // salva novo
          if (!this.vId) {
            alert('lançamento sem ID. Favor contatar suporte antes de prosseguir.')
          }
          fetch(form.action, {
            method: 'POST',
            body: new FormData(form),
          }).then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showPost = !this.showPost
              this.fetchFleet()
              setTimeout(() => {
                this.showPost = !this.showPost
              }, 1500)
            } else if (response.status === 429) {
              alert('O limite de solicitações foi excedido! Entre em contato com o suporte para liberar mais espaço.')
              window.location.reload()
            } else if (response.status === 401 || response.status === 403) {
              alert('Erro de servidor. Entre em contato com o suporte.')
              window.location.reload()
            }
          })
          this.vehicleClose()
        }
      },
      licenceSave () {
        var form = document.getElementById('sheetdb-form-l')
        if (this.editedLIndex > -1) {
          this.licenceDialogSave = true
        } else {
          // salva novo
          fetch(form.action, {
            method: 'POST',
            body: new FormData(form),
          }).then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showPost = !this.showPost
              this.fetchLicences()
              setTimeout(() => {
                this.showPost = !this.showPost
              }, 1500)
            } else if (response.status === 429) {
              alert('O limite de solicitações foi excedido! Entre em contato com o suporte para liberar mais espaço.')
              window.location.reload()
            } else if (response.status === 401 || response.status === 403) {
              alert('Erro de servidor. Entre em contato com o suporte.')
              window.location.reload()
            }
          })
          this.licenceClose()
        }
      },
      editItem (item) {
        this.err.includes('Duplicado')
        ? alert('Não atualizar lançamentos e contatar suporte.')
        : (this.editedIndex = this.companies.indexOf(item), this.company.edited = Object.assign({}, item), this.dialog = true)
      },
      editItemConfirm () {
        // salva edição
        if (this.editedIndex > -1) {
          axios.put(`https://sheetdb.io/api/v1/afxq0nl1f0gvg/ID/${this.editedItem.ID}`, {
            data: {
              SOLICITANTE: `${this.editedItem.SOLICITANTE}`,
              PLACA: `${this.editedItem.PLACA}`,
              NUMTAXA: `${this.editedItem.NUMTAXA}`,
              CUSTOTAXA: `${this.editedItem.CUSTOTAXA}`,
              DATAPAGAMENTOTAXA: `${this.editedItem.DATAPAGAMENTOTAXA}`,
              BANCO: `${this.editedItem.BANCO}`,
              AET: `${this.editedItem.AET}`,
              EMISS\u00c3O: `${this.editedItem.EMISSÃO}`,
              VENCIMENTO: `${this.editedItem.VENCIMENTO}`,
              VALORCOBRADO: `${this.editedItem.VALORCOBRADO}`,
              SALDO: `${this.editedItem.SALDO}`,
              STATUS: `${this.editedItem.STATUS}`,
              NUMBOLETO: `${this.editedItem.NUMBOLETO}`,
              VALORBOLETO: `${this.editedItem.VALORBOLETO}`,
              VENCIMENTOBOLETO: `${this.editedItem.VENCIMENTOBOLETO}`,
              PAGAMENTOBOLETO: `${this.editedItem.PAGAMENTOBOLETO}`,
              OBSERVA\u00c7\u00c3O: `${this.editedItem.OBSERVAÇÃO}`,
              EDICAO: `${this.editedItem.EDICAO}` + ` ${this.user} ${this.timeNow}.`,
            },
          })
            .then((response) => {
              if (response.status === 200 || response.status === 201) {
                this.showUpdate = !this.showUpdate
                this.fetchLicences()
                setTimeout(() => {
                  this.showUpdate = !this.showUpdate
                }, 1500)
              } else {
                this.showError = !this.showError
                setTimeout(() => {
                  this.showError = !this.showError
                  window.location.reload()
                }, 1500)
              }
            })
          this.dialogSave = false
          setTimeout(() => {
            this.close()
          }, 200)
        } else {
          this.save()
        }
      },
      getStates() {
        fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados?orderBy=nome`)
        .then(r => r.json())
        .then((data) => {
          this.states = data
        })
      },
      getCitiesOrigin() {
        fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados/${this.originState}/municipios`)
        .then(r => r.json())
        .then((data) => {
          this.originCities = data
        })
      },
      getCitiesDestination() {
        fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados/${this.destinationState}/municipios`)
        .then(r => r.json())
        .then((data) => {
          this.destinationCities = data
        })
      },
      greaterCompanyId () {
        axios.get('https://sheetdb.io/api/v1/8nmjdq4kt9z0x').then((r) => {
          // console.log(r)
          // console.log(r.data)
          // console.log(r.data.length)
          this.company.edited.ID = r.data.length + 1
        })
      },
    },
  }
</script>

